.header {
  text-align: center;
}

.carousel-container {
  margin: 10px;
}

.selected-image {
  width: 100%;
  height: 500px;
  margin-bottom: 8px;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}

.carousel {
  position: relative;
  margin-bottom: 0px;

}

.carousel__images {
  display: flex;
  max-width: 100%;
  overflow-x: hidden;
}

.carousel__image-selected {
  border: 3px solid #ffa700 !important;
}

.carousel__image {
  margin-right: 10px;
  height: 150px;
  min-width: 150px;
  border: 3px solid #ffa70000;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}

.carousel__button {
  position: absolute;
  top: 40%;
}

.carousel__button-left {
  left: 10px;
}

.carousel__button-right {
  right: 10px;
}




@media (max-width: 375px) {
   .selected-image {

  height: 250px;
  width: 300px;
   margin: 0px;
    padding: 0px;
   }
   .carousel__image {
       height: 100px;
       width: 100px;
        margin: 0px;
    padding: 0px;
   }
   .carousel-container {
   margin: 0px;
   padding: 0px;
}

}
