@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap);
.avatar {
  border-radius: 50%;
  height: 20px;
  width: 20px;
  /* margin-top: -20px; */
  border: 2px solid #fff;
}



.msg {
  display: flex;
  padding: 20px 10px 0 20px;
  margin: 20px;
  border-radius: 3000px;
  box-shadow: 0 0 10px rgb(164, 164, 164);
  align-items: center;
}

.sent {
  background-color: #FF8E53 ;
  color: white;
  border-top-right-radius: 1000px;
  flex-direction: row-reverse;
  padding: 10px 20px 10px 10px;
  text-align: end;
  float: right;
}

.received {
  border: 1px solid lightgray;
  background-color: #FE6B8B;
  border-top-left-radius: 1000px;
  float: left;
}

.sendMsg {
  position: relative;
  display: flex;
  width: 100%;
  bottom: 0;
  z-index: 1;
  border-top: 1px solid lightgray;
  margin-left: -5px;
  padding: 10px;
  padding-bottom: 30px;
  background-color: #fafafa;
}

.msgs {
  margin: 10px 0;
  display: flex;
  flex-direction: column;
}

*{
    -ms-overflow-style: none;
}
::-webkit-scrollbar {
    display: none;
}



@media (max-width: 775px) {
  p {
    font-size: 20px;
  }
  .sent {
    padding: 10px 10px 0 10px;
  }
  .received {
    padding: 10px 10px 0 10px;
  }
  .avatar {
    height: 20px;
    margin-top: -10px;
  }
}

@media (max-width: 500px) {
  p {
    font-size: 15px;
  }
  .sent {
    padding: 7px 7px 0 7px;
  }
  .received {
    padding: 7px 7px 0 7px;
  }
  .avatar {
    height: 20px;
    margin-top: -7px;
  }
}
.header {
  text-align: center;
}

.carousel-container {
  margin: 10px;
}

.selected-image {
  width: 100%;
  height: 500px;
  margin-bottom: 8px;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}

.carousel {
  position: relative;
  margin-bottom: 0px;

}

.carousel__images {
  display: flex;
  max-width: 100%;
  overflow-x: hidden;
}

.carousel__image-selected {
  border: 3px solid #ffa700 !important;
}

.carousel__image {
  margin-right: 10px;
  height: 150px;
  min-width: 150px;
  border: 3px solid #ffa70000;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}

.carousel__button {
  position: absolute;
  top: 40%;
}

.carousel__button-left {
  left: 10px;
}

.carousel__button-right {
  right: 10px;
}




@media (max-width: 375px) {
   .selected-image {

  height: 250px;
  width: 300px;
   margin: 0px;
    padding: 0px;
   }
   .carousel__image {
       height: 100px;
       width: 100px;
        margin: 0px;
    padding: 0px;
   }
   .carousel-container {
   margin: 0px;
   padding: 0px;
}

}

