* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: rgb(248, 246, 253);
}

body,
input,
textarea,
button {
  font-family: "Inter", sans-serif;
}

.app {
  width: 400px;
  margin: 0 auto;
  height: 100vh;
}

/* Contact.js */
.form {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.form > h1 {
  margin-bottom: 30px;
}

.form > input,
textarea {
  padding: 20px;
  border-radius: 3px;
  /* box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.137); */
  margin-bottom: 20px;
  border: 1px solid lightgray;
  /* border: none; */
  background: #fff;
  font-size: 16px;
  color: rgb(0, 0, 32);
  outline: none;
}

.form > input:focus,
textarea:focus {
  border: 1px solid rgb(0, 0, 196);
}

.form > textarea {
  height: 150px;
  max-width: 400px;
  min-height: 100px;
}

.form > label {
  padding-bottom: 10px;
  color: rgb(0, 0, 32);
  font-weight: bold;
  text-align: left;
  padding-left: 5px;
}

.form > button {
  padding: 15px;
  border: none;
  font-weight: 500;
  font-size: 16px;
  border-radius: 3px;
  color: #fff;
  cursor: pointer;
  transition: 0.2s ease-in-out;
  margin-top: 10px;
}


.form > button:hover {
   background: "linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)";
}

.contact {
    width: 75vw;
    /* margin: auto; */
    margin-left: 5em;
    margin-top: 1em;
    /* text-align: left; */
}

.title {
    font-size: 20px;
    margin: 2em 1em 1em 0em;
}

@media (min-width: 875px) {
    .contact {
        width: 50vw;
        margin: auto;
    }
    .title {
        font-size: 24px;
    }

}   
.App {
  text-align: center;
  margin-left: 3em;
 
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

p {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-weight: 500;
  font-size: 15px;
  margin-top: 10px;
  margin-left: 10px;
  margin-right: 10px;
  overflow-wrap: break-word;
}

a:link {
  text-decoration: none;
  color: inherit;
}








